import React, {useState} from 'react'
import ResultList from './ResultList'
import monchService from '../services/monch'



const Right = ({pageType}) => {

    const [ resultList, setResultList ] = useState([])

    const handleSearch = () => {
        const searchTerms = {
            term: document.getElementById('searchTerm').value, 
            location: document.getElementById('searchLocation').value
        }
    
        monchService
            .searchTemp(searchTerms)
            .then(response => setResultList(response))
            .catch(e => console.log(e))
    
    }

    if (pageType === 'homeRight'){
        return(
            <div>
                <div>
                    <input id='searchTerm' type='text' autoComplete='off'/>
                    <input id='searchLocation' type='text' autoComplete='off' value='san francisco, ca' readOnly = 'readOnly'/>
                    <button onClick={handleSearch}>Search</button>
                </div>
                <div>
                    Yelp: 
                    {resultList.map((item, idx) => 
                        <ResultList key={idx} item={item}/>)}
                </div>
            </div>
        )
    }

    return(
        <div>
            Testing right state
        </div>
    )
    
}

export default Right