import axios from 'axios'
const baseUrl = 'api/list'

const getAllTemp = () => {
    const request = axios.get(baseUrl)
    return request.then(response => response)
}

const searchTemp = (searchTerms) => {
    const request = axios.get(`${baseUrl}/searchResult/`, searchTerms)
    return request.then(response => response)
}

export default{ getAllTemp, searchTemp}